import { useEffect, useRef, useState, useCallback } from 'preact/hooks'
import PlayIcon from '../../../../assets/icons/play.jsx'
import PauseIcon from '../../../../assets/icons/pause.jsx'
import {h} from 'preact'
import style from './style'

function AudioPlayerUI({ canPlay, state, handler, timer, pulse, className = '' }) {

    return (
        <div className={`flex rounded w-full border-gray-200 border-2 h-[34px] items-center pl-1 shadow hover:border-gray-300 ${className} ${canPlay || 'opacity-30 pointer-events-none'}`}>

            <div className={`w-[28px] h-[28px] ml-1 rounded-full bg-primary text-black flex cursor-pointer active:scale-95 justify-center items-center ${pulse? style.playerBtn : ''}`} onClick={handler}>
                {state == 'playing' ? <PauseIcon size="18" width="2" /> : <PlayIcon size="18" width="2" className="ml-1" />}
            </div>

            <div className="flex-grow ml-3 rounded h-2 overflow-hidden mr-3 bg-gray-300">
                <div className="w-full h-full bg-primary origin-left duration-200 ease-linear rounded" style={{ transform: `scaleX(${timer})` }}></div>
            </div>
        </div>
    );
}

export function AudioPlayer(props) {
    const [timer, updateTimer] = useState(0);
    const [state, updateState] = useState('stopped');
    const [canPlay, updateCanplay] = useState(false);
    const [pulse, updatePulse] = useState(true);

    let player = useRef(null);

    useEffect(() => {
        let audioURI = props.src;

        if (props.type == "chunk") {
            let blob = new Blob(props.src, { 'type': 'audio/ogg; codecs=opus' });
            audioURI = URL.createObjectURL(blob);
        }

        player.current = new Audio(audioURI);

        if(props.player) props.player.current = player.current;

        // Events
        player.current.addEventListener('pause', () => updateState("stopped"));
        player.current.addEventListener('play', () => {
            updateState("playing");
            updatePulse(false);
        });
        player.current.addEventListener('ended', () => {
            updateState("stopped");
            updateTimer(0);
        });
        player.current.addEventListener('timeupdate', () => updateTimer(player.current.currentTime / player.current.duration));
        player.current.addEventListener('loadedmetadata', () => {
            updateCanplay(true);

            if (player.current.duration === Infinity) {
                player.current.currentTime = 1e101;
                player.current.addEventListener('timeupdate', getDuration);
            }

            function getDuration() {
                player.current.currentTime = 0;
                player.current.removeEventListener('timeupdate', getDuration);
            }
        });

        return () => player.current.stop();
    }, []);

    let clickHandler = useCallback(() => {
        if (state == "stopped") player.current.play();
        if (state == "playing") player.current.pause();
    }, [player, state]);

    return <AudioPlayerUI canPlay={canPlay} pulse={pulse} state={state} className={props.className || ''} timer={timer} handler={clickHandler} />;
}
