import style from './style.css'
import { AudioPlayer } from '../player'
import VoiceSrc from '../../../../assets/audio/pre-launch.ogg'
import {h} from 'preact'
import { useCallback, useEffect, useRef } from 'preact/hooks'

export default function Widget({ isPreview, theme, name, designation, desc, img }) {
    const player = useRef(null);

    const clickHandler = useCallback(()=> {
        player?.current?.play();
    }, [player]);

    return (
        <div className={`${style.widget} ${theme} cursor-pointer`} onClick={clickHandler}>
            <div className={style["widget-row-1"]}>
                <img {...(img ? { src: img } : {})} className={style["widget-dp"]}/>
                <div className={style["widget-author"]}>
                    <div className={style["widget-name"]}>{name}</div>
                    <div className={style["widget-desg"]}>{designation}</div>
                </div>
            </div>
            <div className={style["widget-desc"]}>{desc}</div>
            <AudioPlayer src={VoiceSrc} className="mt-5" player={player}/>
        </div>
    );
}